"use client";
import React from "react";
import Image from "next/image";
import feature from "../../public/images/features.png";
import AOS from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
// ..

const Hero = () => {
  useEffect(() => {
    AOS.init({ duration: 1000 });
  });
  return (
    <div className="flex flex-col sm:flex-row px-4 mt-10 mb-20">
      <div className=" w-full sm:w-1/2 items-start space-y-6 mt-4">
        <h1 className=" hero_heading">Want anything to be easy with VPN</h1>
        <p className=" leading-relaxed text-[15px] md:text-[25px] lg:text-xl">
          A VPN, or Virtual Private Network, is a service that creates a secure,
          encrypted connection over the internet.
        </p>
        <button className="bg-primary rounded-md text-white py-2 px-6 shadow-lg text-[20px] cursor-pointer">
          Get Started
        </button>
      </div>
      <div className=" flex  w-full sm:w-1/2  justify-center items-center">
        <Image data-aos="fade-up" src={feature} alt="Hero" width={400} height={400}></Image>
      </div>
    </div>
  );
};

export default Hero;
